import React, { Component } from 'react'
import classnames from 'classnames';
import Select from "../../common/Select"

export default class Contact_Information extends Component {
    constructor(props) {
        super(props);
        this.state = {
            select: "",
            contact: {}
        }
    }
    setSelect = (select) => {
        this.setState({ select: select });
    };

    onSelectChange = (e) => {
        const {contact} = this.state
        const payload = {
            ...contact,
            [e[0].name]: e.val()
        }
        this.setState({
            contact: payload
        })
        this.props.onChange(payload);
    };

    onInputChange = (e) => {
        const {contact} = this.state
        const payload = {
            ...contact,
            [e.target.name]: e.target.value 
        }
        this.setState({
            contact: payload
        })
        this.props.onChange(payload);
    }


    render() {
        const {contact} = this.state
        const {errors} = this.props
        return (
            <div className="panel-body">
                <div className="form-group">
                    <div className="header-title">Thng tin lin h</div>
                </div>
                <div className="row form-group">
                    <div className={classnames("col-sm-6",{"has-danger": errors && errors.mobile})}>
                        <label className="control-label">in thoi (*)</label>
                        <input required type="number" className="form-control" placeholder="in thoi" name="mobile" onChange={this.onInputChange} required/>
                        {errors && (
                            <div className="errorMessage text-danger m-t-5" style={{fontSize: "smaller"}}>
                                <span>{errors.mobile}</span>
                            </div>
                        )}
                    </div>
                    <div className="col-sm-6">
                        <label className="control-label">S ni b (*)</label>
                        <input required type="number" className="form-control" placeholder="S ni b" defaultValue={contact.company_phone} name="company_phone" onChange={this.onInputChange} />
                        <div className="errorMessage text-danger m-t-5"><span></span></div>
                    </div>
                    <div className={classnames("col-sm-12",{"has-danger": errors && errors.email})}>
                        <label className="control-label">Email (*)</label>
                        <input required type="email" className="form-control" placeholder="Email" name="email" onChange={this.onInputChange} required/>
                        {errors && (
                            <div className="errorMessage text-danger m-t-5" style={{fontSize: "smaller"}}>
                                <span>{errors.email}</span>
                            </div>
                        )}
                    </div>
                </div>
                <div className="row form-group">
                    <div className="col-sm-6 form-group">
                        <label className="control-label">Thng tr (*)</label>
                        <input required type="text" className="form-control" placeholder="Thng tr" name="address" onChange={this.onInputChange} required/>
                        <div className="errorMessage text-danger m-t-5"><span></span></div>
                    </div>
                    <div className="col-sm-6 form-group">
                        <label className="control-label">a ch (*)</label>
                        <input required type="text" className="form-control" placeholder="a ch" name="extra_address1" onChange={this.onInputChange} />
                        <div className="errorMessage text-danger m-t-5"><span></span></div>
                    </div>
                    <div className="col-sm-6">
                        <label className="control-label">Ch  hin ti (*)</label>
                        <input required type="text" className="form-control" placeholder="Ch  hin ti" name="contact_address" onChange={this.onInputChange} />
                        <div className="errorMessage text-danger m-t-5"><span></span></div>
                    </div>
                    <div className="col-sm-6">
                        <label className="control-label">a ch (*)</label>
                        <input required type="text" className="form-control" placeholder="a ch" name="extra_address2" onChange={this.onInputChange} />
                        <div className="errorMessage text-danger m-t-5"><span></span></div>
                    </div>
                </div>
            </div>
        )
    }
}
